import { vec3, mat4 } from 'gl-matrix';
import { Renderer, createTransforms, createGPUBuffer } from '../../';

export class SphereWire {

    numberOfVertices: number = 0;
    vertexBuffer;
    normalBuffer;
    uvBuffer;
    uniformBindGroup;
    uniformBuffer;

    commandEncoder;

    textureView;
    renderPassDescription;
    pipeline

    isAnimation = true;
    renderer: Renderer;
    modelMatrix: mat4;
    translation: vec3 = vec3.fromValues(0,0,0)
    rotation: vec3 = vec3.fromValues(0,0,0)
    constructor(renderer: Renderer) {
        this.renderer = renderer;


        // create vertex buffers
        const sphereData = SphereData(1.5, 70, 40);
        this.numberOfVertices = (sphereData?.vertexData!).length/3;
        this.vertexBuffer = createGPUBuffer(this.renderer.device, sphereData?.vertexData!);   
        this.normalBuffer = createGPUBuffer(this.renderer.device, sphereData?.normalData!);
        this.uvBuffer = createGPUBuffer(this.renderer.device, sphereData?.uvData!);

        const shader = Shaders();
        this.pipeline = this.renderer.device.createRenderPipeline({
            layout: 'auto',
            vertex: {
                module: this.renderer.device.createShaderModule({
                    code: shader.vertex
                }),
                entryPoint: "main",
                // @ts-ignore
                buffers: [
                    {
                        arrayStride: 12,
                        attributes: [{
                            shaderLocation: 0,
                            format: "float32x3",
                            offset: 0
                        }]
                    }
                ]
            },
            fragment: {
                module: this.renderer.device.createShaderModule({
                    code: shader.fragment
                }),
                entryPoint: "main",
                targets: [
                    {
                        format: this.renderer.format as GPUTextureFormat
                    }
                ]
            },
            primitive: {
                topology: "line-list",
            },
            depthStencil: {
                format: "depth24plus",
                depthWriteEnabled: true,
                depthCompare: "less"
            }
        });

        // // create uniform data
        this.modelMatrix = mat4.create();
        // const mvpMatrix = mat4.create();
        // let vMatrix = mat4.create();
        // let vpMatrix = mat4.create();
        // const vp = CreateViewProjection(gpu.canvas.width / gpu.canvas.height);
        // vpMatrix = vp.viewProjectionMatrix;

        // add rotation and camera:
        this.rotation = vec3.fromValues(0, 0, 0);
        // var camera = createCamera(gpu.canvas, vp.cameraOption);

        // create uniform buffer and layout
        this.uniformBuffer = this.renderer.device.createBuffer({
            size: 64,
            usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST
        });

        this.uniformBindGroup = this.renderer.device.createBindGroup({
            layout: this.pipeline.getBindGroupLayout(0),
            entries: [{
                binding: 0,
                resource: {
                    buffer: this.uniformBuffer,
                    offset: 0,
                    size: 64
                }
            }]
        });

        this.textureView = this.renderer.ctx.getCurrentTexture().createView();
        const depthTexture = this.renderer.device.createTexture({
            size: [this.renderer.canvas.width, this.renderer.canvas.height, 1],
            format: "depth24plus",
            usage: GPUTextureUsage.RENDER_ATTACHMENT
        });
        this.renderPassDescription = {
            colorAttachments: [{
                view: this.textureView,
                clearValue: { r: 0.2, g: 0.247, b: 0.314, a: 1.0 }, //background color
                loadOp: 'clear',
                storeOp: 'store'
            }],
            depthStencilAttachment: {
                view: depthTexture.createView(),
                depthClearValue: 1.0,
                depthLoadOp: 'clear',
                depthStoreOp: "store",
                //stencilLoadValue: 0,
                //stencilStoreOp: "store"
            }
        };

    }

    //     CreateAnimation(draw, rotation, isAnimation);
    // }
    render(commandEncoder, i) {
        if (!this.isAnimation) {
            // if (camera.tick()) {
                // const pMatrix = vp.projectionMatrix;
                // vMatrix = camera.matrix;
                // mat4.multiply(vpMatrix, pMatrix, vMatrix);
            // }
        }

//         const commandEncoder = device.createCommandEncoder()
// const computePass = commandEncoder.beginComputePass()
// computePass.setPipeline(pipelineObj.computePipeline)
// computePass.setBindGroup(0, pipelineObj.computeGroup)
// computePass.dispatchWorkgroups(Math.ceil(NUM / 128))
// computePass.end()

        createTransforms(this.modelMatrix, this.translation, this.rotation);
        mat4.multiply(this.renderer.camera.mvpMatrix,this.renderer.camera. vpMatrix, this.modelMatrix);
        this.renderer.device.queue.writeBuffer(this.uniformBuffer, 0, this.renderer.camera.mvpMatrix as ArrayBuffer);


        // contextTexture.destroy();
        // texture.destroy();
        
        // contextTexture = gpu.context.getCurrentTexture();      
        // texture = device.createTexture({
        //     size: [gpu.canvas.width, gpu.canvas.height, 1],
        //     sampleCount: 4,
        //     format: gpu.format as GPUTextureFormat,
        //     usage: GPUTextureUsage.RENDER_ATTACHMENT,
        // });
        // renderPassDescription.colorAttachments[0].resolveTarget = contextTexture.createView();
        // renderPassDescription.colorAttachments[0].view = texture.createView() as GPUTextureView;

        // const commandEncoder = device.createCommandEncoder();
        // const renderPass = commandEncoder.beginRenderPass(renderPassDescription as GPURenderPassDescriptor);

        this.textureView = this.renderer.ctx.getCurrentTexture().createView();
        this.renderPassDescription.colorAttachments[0].view = this.textureView;
        
        const renderPass = commandEncoder.beginRenderPass(this.renderPassDescription as GPURenderPassDescriptor);

        renderPass.setPipeline(this.pipeline);
        renderPass.setVertexBuffer(0, this.vertexBuffer);
        renderPass.setBindGroup(0, this.uniformBindGroup);
        renderPass.draw(this.numberOfVertices);
        renderPass.end();

        // this.renderer.device.queue.submit([this.commandEncoder.finish()]);
    }
}
export const Shaders = () => {
    const vertex = `
        struct Uniforms {
            mvpMatrix : mat4x4<f32>,
        };
        @binding(0) @group(0) var<uniform> uniforms : Uniforms;
        
        @vertex
        fn main(@location(0) pos: vec4<f32>) ->  @builtin(position) vec4<f32> {
            return uniforms.mvpMatrix * pos;     
        }`;

    const fragment = `
        @fragment
        fn main() -> @location(0) vec4<f32> {
            return vec4<f32>(1.0, 1.0, 0.0, 1.0);            
        }`;

    return {
        vertex, 
        fragment
    };
}


export const SphereData = (radius:number = 2, u:number = 20, v:number = 15, center:vec3 = [0,0,0], ul = 1, vl = 1) => {
    if(u<2 || v<2) return;
    let pts = [];
    let pt:vec3;
    for(let i = 0;i<u;i++){
        let pt1:vec3[] = [];
        for(let j=0;j<v;j++){
            pt = SpherePosition(radius, i*180/(u-1), j*360/(v-1), center);                
            pt1.push(pt);
        }
        pts.push(pt1);
    }

    let vertex = [] as any, normal = [] as any;
    let uv = [] as any;
    let u0, u1, u2,u3, v0,v1,v2,v3;
    let p0:vec3,p1:vec3,p2:vec3,p3:vec3,a:vec3, b:vec3;

    for(let i=0;i<u-1;i++){
        for(let j=0;j<v-1;j++){
            p0 = pts[i][j];
            p1 = pts[i+1][j];
            p2 = pts[i+1][j+1];
            p3 = pts[i][j+1];
            a = vec3.create();
            b = vec3.create();
            vec3.subtract(a, p2,p0);
            vec3.subtract(b, p1,p3); 
            
            // vertex data
            vertex.push([
                //first triangle                    
                p0[0],p0[1],p0[2], p1[0],p1[1],p1[2], p3[0],p3[1],p3[2],  

                //second triangle                    
                p1[0],p1[1],p1[2], p2[0],p2[1],p2[2], p3[0],p3[1],p3[2]  
            ]);

            //normal data
            normal.push([
                //first triangle
                p0[0]/radius,p0[1]/radius,p0[2]/radius,
                p1[0]/radius,p1[1]/radius,p1[2]/radius,
                p3[0]/radius,p3[1]/radius,p3[2]/radius,

                //second triangle                    
                p1[0]/radius,p1[1]/radius,p1[2]/radius, 
                p2[0]/radius,p2[1]/radius,p2[2]/radius,
                p3[0]/radius,p3[1]/radius,p3[2]/radius
            ]);

            // uv data
            u0 = ul * (0.5+Math.atan2(p0[0]/radius,p0[2]/radius)/Math.PI/2);
            u1 = ul * (0.5+Math.atan2(p1[0]/radius,p1[2]/radius)/Math.PI/2);
            u2 = ul * (0.5+Math.atan2(p2[0]/radius,p2[2]/radius)/Math.PI/2);
            u3 = ul * (0.5+Math.atan2(p3[0]/radius,p3[2]/radius)/Math.PI/2);
            v0 = vl * (0.5-Math.asin(p0[1]/radius)/Math.PI);
            v1 = vl * (0.5-Math.asin(p1[1]/radius)/Math.PI);
            v2 = vl * (0.5-Math.asin(p2[1]/radius)/Math.PI);
            v3 = vl * (0.5-Math.asin(p3[1]/radius)/Math.PI);

            uv.push([
                //first triangle
                u0, v0, u1, v1, u3, v3,                   

                //second triangle 
                u1, v1, u2, v2, u3, v3                   
            ]);
        }
    }
    return {
        vertexData: new Float32Array(vertex.flat()),
        normalData: new Float32Array(normal.flat()),
        uvData: new Float32Array(uv.flat())
    }
};

export const SpherePosition = (radius:number, theta:number, phi:number, center:vec3 = [0,0,0]) => {
    let snt = Math.sin(theta*Math.PI/180);
    let cnt = Math.cos(theta*Math.PI/180);
    let snp = Math.sin(phi*Math.PI/180);
    let cnp = Math.cos(phi*Math.PI/180);
    return vec3.fromValues(radius*snt*cnp + center[0], radius*cnt + center[1], -radius*snt*snp + center[2]);     
};