import { vec3, mat4 } from 'gl-matrix';

import { loadWGSL, Renderer ,  } from '../';
import { CubeData } from '../shaders/cube';

import { createTransforms } from '../';

const shaderPath = require('../shaders/sha.wgsl');

export class Cube {
    renderer: Renderer
    // ctx: GPUCanvasContext;
    // device: GPUDevice;
    // format: GPUTextureFormat;
    size: any;
    pipeline: GPURenderPipeline;

    rotation: vec3;


    normalMatrix: mat4;
    modelMatrix: mat4;
    translateMatrix: mat4;


    uniformBuffer: GPUBuffer
    uniformBindGroup: GPUBindGroup
    textureView: GPUTextureView
    depthTexture: GPUTexture;
    renderPassDescription: GPURenderPassDescriptor

    numberOfVertices: number;
    vertexBuffer: GPUBuffer;
    colorBuffer: GPUBuffer;

    constructor(renderer: Renderer) {
        this.renderer = renderer;
        // const sphereData = SphereData(1.5, 70, 40);
        // this.numberOfVertices = (sphereData?.vertexData!).length/3;
        // this.vertexBuffer = CreateGPUBuffer(device, sphereData?.vertexData!);   
        // this.normalBuffer = CreateGPUBuffer(device, sphereData?.normalData!);
        // this.uvBuffer = CreateGPUBuffer(device, sphereData?.uvData!);
        this.normalMatrix = mat4.create();  
        this.modelMatrix = mat4.create();
        this.translateMatrix = mat4.create();
        createTransforms(this.translateMatrix,[0,0,0], [0,0,0], [1,1,1]);

        // console.log(sphereShader)
        this.initPipeline()
    }
      async initPipeline(primitiveType = 'triangle-list') { // triangle-list , triangle-strip
        // create vertex buffers
        const cubeData = CubeData();
        this.numberOfVertices = cubeData.positions.length / 3;
        this.vertexBuffer = CreateGPUBuffer(this.renderer.device, cubeData.positions);
        this.colorBuffer = CreateGPUBuffer(this.renderer.device, cubeData.colors);

        console.log(shaderPath)

        const shader = await loadWGSL(shaderPath);

        this.pipeline = this.renderer.device.createRenderPipeline({
            layout: 'auto',
            vertex: {
                module: this.renderer.device.createShaderModule({
                    code: shader
                }),
                entryPoint: "vs_main",
                // @ts-ignore
                buffers: [
                    {
                        arrayStride: 12,
                        attributes: [{
                            shaderLocation: 0,
                            format: "float32x3",
                            offset: 0
                        }]
                    },
                    {
                        arrayStride: 12,
                        attributes: [{
                            shaderLocation: 1,
                            format: "float32x3",
                            offset: 0
                        }]
                    }
                ]
            },
            fragment: {
                module: this.renderer.device.createShaderModule({
                    code: shader
                }),
                entryPoint: "fs_main",
                targets: [
                    {
                        format: this.renderer.format as GPUTextureFormat
                    }
                ]
            },
            primitive: {
                topology: "triangle-list",
                cullMode: 'back'
            },
            depthStencil: {
                format: "depth24plus",
                depthWriteEnabled: true,
                depthCompare: "less"
            }
        });

        // create uniform buffer and layout
        this.uniformBuffer = this.renderer.device.createBuffer({
            size: 64,
            usage: GPUBufferUsage.UNIFORM | GPUBufferUsage.COPY_DST
        });


        this.uniformBindGroup = this.renderer.device.createBindGroup({
            layout: this.pipeline.getBindGroupLayout(0),
            entries: [{
                binding: 0,
                resource: {
                    buffer: this.uniformBuffer,
                    offset: 0,
                    size: 64
                }
            }]
        });

        this.textureView = this.renderer.ctx.getCurrentTexture().createView();
        this.depthTexture = this.renderer.device.createTexture({
            size: [this.renderer.canvas.width, this.renderer.canvas.height, 1],
            format: "depth24plus",
            usage: GPUTextureUsage.RENDER_ATTACHMENT
        });
        this.renderPassDescription = {
            colorAttachments: [{
                view: this.textureView,
                clearValue: { r: 0.0, g: 0.0, b: 0.0, a: 1.0 }, //background color
                //@ts-ignore
                loadValue: { r: 0.0, g: 0.0, b: 0.0, a: 1.0 },
                loadOp: 'clear',
                storeOp: 'store'
            }],
            depthStencilAttachment: {
                view: this.depthTexture.createView(),
                depthClearValue: 1.0,
                depthLoadOp: 'clear',
                depthStoreOp: "store",
            }
        };

        // this.requestAni = requestAnimationFrame(this.loop);
    }
    public render() {

        // //transforms on sphere
        // mat4.rotate(
        //     this.modelMatrix, 
        //     this.translateMatrix, 
        //     1, 
        //     vec3.fromValues(Math.cos(this.rotation[0]*2), Math.sin(this.rotation[0]*2), 0)
        // );            
        // mat4.invert(this.normalMatrix, this.modelMatrix);
        // mat4.transpose(this.normalMatrix, this.normalMatrix);
        // this.renderer.device.queue.writeBuffer(this.vertexUniformBuffer, 64, this.modelMatrix as ArrayBuffer);
        // device.queue.writeBuffer(this.vertexUniformBuffer, 128, this.normalMatrix as ArrayBuffer);  
        
        // //transforms on cube
        // mat4.rotate(
        //     this.modelMatrix, 
        //     this.translateMatrix, 
        //     1, 
        //     vec3.fromValues(Math.cos(this.rotation[1]*2), 0, Math.sin(this.rotation[2]*2))
        // );        
        // mat4.multiply(modelViewProjectionMatrix, vp.viewMatrix, this.modelMatrix);
        // mat4.multiply(modelViewProjectionMatrix, vp.projectionMatrix, modelViewProjectionMatrix2);      

        // device.queue.writeBuffer(uniformBuffer2, 0, modelViewProjectionMatrix2 as ArrayBuffer);  
        

        mat4.multiply(this.translateMatrix, this.renderer.camera.mvpMatrix, this.modelMatrix);
        mat4.multiply(this.translateMatrix, this.renderer.camera.projection, this.translateMatrix);   

        this.renderer.device.queue.writeBuffer(this.uniformBuffer, 0, this.translateMatrix as ArrayBuffer);

        this.textureView = this.renderer.ctx.getCurrentTexture().createView();
        this.renderPassDescription.colorAttachments[0].view = this.textureView;
        this.renderer.commandEncoder = this.renderer.device.createCommandEncoder();
        const renderPass = this.renderer.commandEncoder.beginRenderPass(this.renderPassDescription as GPURenderPassDescriptor);

        // this.device.queue.writeBuffer(this.uniformBuffer, 0, this.renderer.camera.mvpMatrix as ArrayBuffer);

        // this.textureView = this.ctx.getCurrentTexture().createView();
        // this.renderPassDescription.colorAttachments[0].view = this.textureView;
        // const commandEncoder = this.device.createCommandEncoder();
        // const renderPass = commandEncoder.beginRenderPass(this.renderPassDescription as GPURenderPassDescriptor);


        renderPass.setPipeline(this.pipeline);
        renderPass.setVertexBuffer(0, this.vertexBuffer);
        renderPass.setVertexBuffer(1, this.colorBuffer);
        renderPass.setBindGroup(0, this.uniformBindGroup);
        renderPass.draw(this.numberOfVertices);
        renderPass.end();
    
        this.renderer.device.queue.submit([this.renderer.commandEncoder.finish()]);
    }
}

export const CreateGPUBuffer = (device:GPUDevice, data:Float32Array, 
    usageFlag:GPUBufferUsageFlags = GPUBufferUsage.VERTEX | GPUBufferUsage.COPY_DST) => {
    const buffer = device.createBuffer({
        size: data.byteLength,
        usage: usageFlag,
        mappedAtCreation: true
    });
    new Float32Array(buffer.getMappedRange()).set(data);
    buffer.unmap();
    return buffer;
}