import { vec3, mat4 } from 'gl-matrix';

import { Renderer, createViewProjection, createTransforms } from '.';

export class NCamera {
    public solution: Renderer;
    public canvas: HTMLCanvasElement
    public modelMatrix: mat4 // const
    public mvpMatrix: mat4 // const

    public vMatrix: mat4;
    public vpMatrix: mat4;
    public rotation =vec3.fromValues(0, 0, 0); 
    public translation =vec3.fromValues(0, 0, 0); 

    public vp: any // const

    public projection = mat4.create();

    public eye = vec3.fromValues(1,1,0);
    public center = vec3.fromValues(0, 0, 0);
    public up = vec3.fromValues(0, 1, 0)

    constructor(solution: Renderer) {
        this.solution = solution;
        this.canvas = solution.canvas;

        this.modelMatrix = mat4.create();
        this.mvpMatrix = mat4.create();
        this.vMatrix = mat4.create();
        this.vpMatrix = mat4.create();
        
        this.vp = createViewProjection(this.canvas.width / this.canvas.height);
        this.vpMatrix = this.vp.viewProjectionMatrix;

        // add rotation and camera:
        // this.camera = createCamera(this.canvas, this.vp.cameraOption);

        // this.vp = createViewProjection(this.canvas.width / this.canvas.height);
        // this.vpMatrix = this.vp.viewProjectionMatrix;

        // add rotation and camera:
        // this.camera = createCamera(this.canvas, this.vp.cameraOption);
        // this.camera.mode = 'turntable' // https://github.com/mikolalysenko/3d-view-controls



        mat4.rotateZ(this.modelMatrix, this.modelMatrix, 0.0);
        mat4.scale(this.modelMatrix, this.modelMatrix, [1, 1, 1]);
        mat4.lookAt(this.vMatrix, this.eye, this.center, this.up);


        // mat4.perspective(this.projectionCamera, Math.PI / 1.5, this.solution.canvas.width / this.solution.canvas.height, .1, 10);

    }
    public update() {

        //
        mat4.lookAt(this.vMatrix, this.eye, this.center, this.up);
    
        // mat4.perspective(this.projectionCamera, Math.PI / 1.5, this.solution.canvas.width / this.solution.canvas.height, .1, 10);
  
        // this.viewCamera = view
        // this.projectionCamera = projection;
        // this.modelCamera = model;


        // mat4.multiply(model, projection, view);

        // // const pMatrix = this.projectionCamera;
        // // this.vMatrix = this.viewCamera;
        // // mat4.multiply(this.vpMatrix, pMatrix, this.vMatrix);
   
        // // createTransforms(this.modelCamera, this.translation, this.rotation);
        // // mat4.multiply(this.mvpMatrix, this.vpMatrix, this.modelCamera);

        
        // const pMatrix = this.vp.projectionMatrix;
        // this.vMatrix = this.camera.matrix;

        mat4.multiply(this.projection, this.vp.projectionMatrix, this.vMatrix);
        createTransforms(this.modelMatrix, this.translation, this.rotation);
        mat4.multiply(this.mvpMatrix, this.projection, this.modelMatrix);
    }
    public rotateZ(val = 0.01) {
        this.rotation[1] += val;
        // mat4.rotateY(this.modelMatrix, this.modelMatrix, val);
    }
    public translateX(val = 0.01) {
        this.translation[0] += val;
    }
};

